<template>
    <div class="content-wrapper">
        <h1>Перевізники</h1>
        <router-link
            to="/manager/carrier/create"
            tag="button"
            class="add-item"
            ><span>+</span><span>Додати перевізника</span></router-link
        >
        <b-table
            class="mt-3 mb-3"
            striped
            hover
            :fields="fields"
            :items="items"
            @row-clicked="show"
        >
            <template #cell(id)="data">
                <span class="id-cell">{{ data.index + 1 }}</span>
            </template>
            <template #cell(carrier)="data">
                <span class="route-name">{{ data.item.carrier }}</span>
            </template>
            <template #cell(routeQuantity)="data">
                <span>{{ data.item.routeQuantity }}</span>
            </template>
            <template #cell(tripQuantity)="data">
                <span>{{ data.item.tripQuantity }}</span>
            </template>
            <template #cell(busQuantity)="data">
                <span>{{ data.item.busQuantity }}</span>
            </template>
            <template #cell(driverQuantity)="data">
                <span>{{ data.item.driverQuantity }}</span>
            </template>
            <template #cell(contact)="data">
                <span>{{ data.item.contact }}</span>
            </template>
            <template #cell(delete)="data">
                <span
                    @click.prevent="deleteItem(data.index, data.item.id)"
                    class="delete-table__button tar"
                >
                    Видалити
                </span>
            </template>
        </b-table>
    </div>
</template>
<script>


export default {
    title: "Carrier",
    data() {
        return {
            // Note `isActive` is left out and will not appear in the rendered table
            fields: [
                {
                    key: "id",
                    label: "№"
                },
                {
                    key: "carrier",
                    label: "Перевізник"
                },
                {
                    key: "routeQuantity",
                    label: "Маршрут"
                },
                {
                    key: "tripQuantity",
                    label: "Рейсів"
                },
                {
                    key: "busQuantity",
                    label: "Автобусів"
                },
                {
                    key: "driverQuantity",
                    label: "Автобусів"
                },
                {
                    key: "contact",
                    label: "Контакти "
                },
                {
                    key: "delete",
                    label: ""
                }
            ],
            items: [
            ]
        };
    },
    created() {
        axios.get("admin/carrier").then(res => {
            this.items = res.data.result.data;
        });
    },
    methods: {
        show(item) {
            this.$router.push(`/manager/carrier/${item.id}`)
        },
        deleteItem(index, id) {
            this.items.splice(index, 1);
            axios.delete("admin/carrier/" + id).then(res => {
                //TODO: Notification delete
                console.log('deleted')
            });
        },
    }
};
</script>

