<template>
    <div class="content-wrapper">
        <h1>Автобуси<span>5</span></h1>
        <router-link
            to="/manager/bus/create"
            tag="button"
            class="add-item"
            ><span>+</span><span>Додати автобус</span></router-link
        >
        <b-table
            class="mt-3 mb-3"
            striped
            hover
            :fields="fields"
            :items="items"
            @row-clicked="show"
        >
            <template #cell(id)="data">
                <span class="id-cell">{{ data.index + 1 }}</span>
            </template>
            <template #cell(number)="data">
                <span class="route-name bold">{{ data.item.number }}</span>
            </template>
            <template #cell(mark)="data">
                <span>{{ data.item.mark }}</span>
            </template>
            <template #cell(seats)="data">
                <span>{{ data.item.seats }}</span>
            </template>
            <template #cell(carrier)="data">
                <span>{{ data.item.carrier }}</span>
            </template>
            <template #cell(delete)="data">
                <span
                    @click.prevent="deleteItem(data.index, data.item.id)"
                    class="delete-table__button tar"
                >
                    Видалити
                </span>
            </template>
        </b-table>
    </div>
</template>
<script>
export default {
    title: "Bus",
    data() {
        return {
            // Note `isActive` is left out and will not appear in the rendered table
            fields: [
                {
                    key: "id",
                    label: "№"
                },
                {
                    key: "number",
                    label: "Номерний знак/ID"
                },
                {
                    key: "mark",
                    label: "Марка"
                },
                {
                    key: "seats",
                    label: "Кільксть місць"
                },
                {
                    key: "carrier",
                    label: "Перевізник"
                },
                {
                    key: "delete",
                    label: ""
                }
            ],
            items: [
            ]
        };
    },
    created() {
        axios.get("admin/bus").then(res => {
            this.items = res.data.result.data;
        });
    },
    methods: {
        show(item) {
            this.$router.push(`/manager/bus/${item.id}`)
        },
        deleteItem(index, id) {
            this.items.splice(index, 1);

            axios.delete("admin/bus/" + id)
                .then(res => {
                    //TODO: Notification delete
                    console.log('deleted')
                });
        }
    }
};
</script>

