<script>
import datePicker from "vue-bootstrap-datetimepicker";
import ModalConfirm from "../../components/ModalConfirm";

export default {
    name: 'TripCreate',
    components: {
        datePicker,
        ModalConfirm,
    },
    data(){
        return{
            // newTripData: {
                id: null,
                number: '',
                carrier: '',
                route: '',
                bus: '',
                type: '',
                driver: '',
                price: '',
                seats: 8,
                cancelPossibility: false,
                departureTime: '',
                departureDate: new Date(),
                arrivalTime: '',
                arrivalDate: new Date(),
                departureAllow: '',
                arrivalWaiting: '',
                timeToClose: '',
                additionalInfo: {

                },
                
            // },
            optionsdate: {
                format: "DD.MM.YYYY",
                useCurrent: false,
                minDate: new Date(),
            },
            modalDeclineIsVisible: false,
            modalDeleteIsVisible: false,
            itemsNumbers: [
                    {
                        id: 1,
                        number: '# 128'
                    },
                    {
                        id: 2,
                        number: '# 1233'
                    },
                    {
                        id: 3,
                        number: '# 125'
                    },
                ]
            
        }
    },
    created(){
        if (this.$route.params.id != 'create'){
            axios.get('manager/trip/'+this.$route.params.id)
                .then((res) => {
                   console.log(res.data.result);
                })
        }
    },
    methods: {        
        addNewTrip(){
            const data = {
                id : this.id,
                number : this.number,
                carrier : this.carrier,
                route : this.route,
                bus : this.bus,
                type : this.type,
                driver : this.driver,
                price : this.price,
                seats : this.seats,
                cancelPossibility : this.cancelPossibility,
                departureTime : this.departureTime,
                departureDate : this.departureDate,
                arrivalTime : this.arrivalTime,
                arrivalDate : this.arrivalDate,
                departureAllow : this.departureAllow,
                arrivalWaiting : this.arrivalWaiting,
                timeToClose : this.timeToClose,
                additionalInfo : this.additionalInfo
            }
            axios.post("manager/trip/create", data)
            .then(res => {
                location.href ='/manager/trip/'
            })
        },
        declineTrip(status){            
            this.modalDeclineIsVisible = status            
        },
        declineTrue(success){
            if(success){
                location.href ='/manager/trip/'
            }            
        },
        deleteTrip(status){
            this.modalDeleteIsVisible = status
        },
        deleteTrue(success){
            if(success){
                axios.delete("manager/trip/" + this.id)
                    .then(res => {
                        location.href ='/manager/trip/'
                    })
            }
            
        },        
    }
}
</script>

