<template>
    <div class="set-price__container">
        <h1>Вартість проїзду на міжміському автобусному маршруті загального користування</h1>
        <button @click="$router.back()" class="back-button d-flex align-items-center justify-content-center mb-4">
            <svg width="11" height="19" viewBox="0 0 11 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M10 1L1.5 9.5L10 18" stroke="white" stroke-width="2"/>
            </svg>
            Назад
        </button>
        <div class="set-price__wrapper p-4">
            <div class="set-price">
                <h1 v-if="isPending">Завантаження</h1>
                <b-card no-body v-else>
                    <b-tabs pills card vertical nav-wrapper-class="w-50 from-list p-0">
                        <b-tab :title="station.start" v-for="(station, index) in pricesData" :key="index" :active="index==0">
                            <div class="d-flex">   
                                <ul>
                                    <li class="d-flex align-items-center" v-for="(stationEnd, ind) in station.prices" :key="ind" >
                                        <span class="station-name" >{{stationEnd.to}}</span>
                                        <span class="input-wrapper position-relative d-flex" >
                                            <input type="text" class="set-route__price" placeholder="Ціна" onkeydown="javascript: return ((event.keyCode>47)&&(event.keyCode<58))" maxlength="4" v-model="pricesData[index].prices[ind].price" @input="setPrice(index, ind)" :test="index" :testt="ind">
                                            <span class="input-currency">грн.</span>
                                        </span>
                                    </li>     
                                </ul>
                            </div>
                        </b-tab>
                    </b-tabs>
                </b-card>
                <button class="accept-button" @click="acceptPrice">Підтвердити ціни</button>

            </div>
        </div>
    </div>
    
</template>
<script>
import { mapState } from 'vuex';
export default {
    name: 'SetPrice',
    data(){
        return{
            isPending: true,
            pricesData: []
        }
    },
    computed: {
        ...mapState([
            'routeData'
        ])
    },
    methods: {
        returnButton(){
            this.$router.go(-1)
        },
        // setInputPrice(e){
        //     // console.log(e.data)
        //     if( e.charCode >= 48 && e.charCode <= 57){
        //         return e.charCode >= 48 && e.charCode <= 57
        //     } else {
        //         return false
        //     }
        //     // value = value.replace(/\D/, '')
        // }
        setPrice(index, ind){
            console.log(this.pricesData[index].prices[ind].price)
        },
        acceptPrice(){
            axios.post('admin/route')
            .then( (res) => {
                console.log()
            })
            .catch(error => console.error(error))
        }
    },
    mounted() {
        if(this.routeData.routeData.length > 0){
            this.isPending = false 
            // console.log(this.routeData.routeData)

            this.pricesData = this.routeData.routeData.map((station, index) => {

                let tempObj = {
                    start: station.location,
                    prices: []
                }

                for( let key in this.routeData.routeData){

                    if(station.location == this.routeData.routeData[key].location){
                        continue 
                    } else{
                        tempObj.prices.push({
                            from: station.location,
                            to: this.routeData.routeData[key].location,
                            from_place_id: station.place_id,
                            to_place_id: this.routeData.routeData[key].place_id,
                            price: this.routeData.routeData[index].price[key],
                        })
                    }
                }
                return tempObj;
            })

            console.log(this.pricesData)

        }
    },
}
</script>
