    },
    metaInfo: {
        title: 'My Example App №2',
        titleTemplate: '%s - Yay! ршршрш',
    },
    created() {
        axios.get("admin/route").then(res => {
            // console.log(res.data.result)
            this.items = res.data.result.data;
        });
    },
    // mounted() {
    //     axios.get("manager/route").then(res => {
    //         this.items = res.data.result;
    //     });
    // },
    methods: {
        show(item) {
            location.href ='/manager/route/'+item.id
        },
        deleteItem(index, id) {
            this.items.splice(index, 1);
            axios.delete("admin/route/" + id).then(res => {
                //todo: Notification delete
                console.log('deleted')
            });
        },
        changeItem(item) {
            // console.log(item.id);
            location.href ='/manager/route/'+item.id
        }
    }
};
</script>

