    methods: {
        show(item) {
            // console.log(item.id);
            location.href ='/manager/trip/'+item.id
        },
        openTripCategory(category, index) {
            if (category == "internationalTrip") {
                this.internationalTrip = true;
                this.regionTrip = false;
                this.cityTrip = false;
            } else if (category == "regionTrip") {
                this.internationalTrip = false;
                this.regionTrip = true;
                this.cityTrip = false;
            } else {
                this.internationalTrip = false;
                this.regionTrip = false;
                this.cityTrip = true;
            }

            this.categoriesList.forEach(item => (item.selected = false));
            this.categoriesList[index].selected = true;
        },
        selectCategory(index) {
            // console.log(index)
            this.directionsList.forEach(item => (item.selected = false));
            this.directionsList[index].selected = true;
        },
        selectSorting() {
            console.log();
        },
        deleteItem(index, id) {
            this.items.splice(index, 1);

            axios.delete("admin/trip/" + id)
                .then(res => {
