        <router-link to="/manager/carrier" class="back-button d-flex align-items-center justify-content-center mb-4">
            <svg width="11" height="19" viewBox="0 0 11 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M10 1L1.5 9.5L10 18" stroke="white" stroke-width="2"/>
            </svg>
            Назад
        </router-link>
        <form class="d-flex  carrier-inner flex-column flex-xxl-row  mb-5 justify-content-between">
            <div class="d-flex flex-column carrier-inner__column px-3 big">
                <div class="d-flex flex-column">
                    <label for="carrier-name" >Найменування</label>
                    <input type="text" name="" id="carrier-name" placeholder="Найменування перевізника"  v-model="name">
                </div>
                <div class="d-flex flex-column">
                    <label for="carrier-address" >Адреса</label>
                    <input type="text" name="" id="carrier-address" placeholder="Адреса" v-model="address">
                </div>
                <div class="d-flex flex-column">
                    <label for="carrier-phone" >Телефон</label>
                    <the-mask
                        class="inp-tel-value mb-4"
