                    ></the-mask>
                </div>
                <div class="d-flex flex-column">
                    <label for="carrier-contact-person">Контактна особа</label>
                    <input type="text" name="" id="carrier-contact-person" placeholder="Контактна особа"  v-model="contact_person">
                </div>
                <label >Платіжні дані</label>
                <div class="carrier-payment__wrapper p-4">
                    <div class="d-flex flex-column">
                        <label for="carrier-liqpay" >Liqpay</label>
                        <input type="text" name="" id="carrier-liqpay" placeholder="liqpay" v-model="liqpay">
                    </div>
                </div>                
            </div>
            <div v-if="id === ''" class="d-flex justify-content-start">
                <span>Щоб додати водіїв, автобуси і мартшрути, необхідно зберегти перевізника</span>
            </div>
            <div class="d-flex w-100" v-else>
                <div class="d-flex flex-column carrier-inner__column">
                    <div class="d-flex flex-column w-100 px-3">
                        <div class="d-flex justify-content-between align-items-center ">
                            <label for="trip-list" >Рейси:</label>
                            <router-link to="/manager/trip/create" class="add-button">
                                + Додати
                            </router-link>
                        </div>
                        <input type="search" class="carrier-form__search" v-model="searchedTrip">
                        <div class="carrier-form__list">
                            <div v-for="(trip, index) in filteredTripList" :key="trip.id" class="d-flex justify-content-between px-2 mb-2 ">
                                <span>{{trip.name}}</span><button @click.prevent="deleteTrip(index,trip.id)" class="delete-item__btn"></button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="d-flex  flex-column carrier-inner__column">
                    <div class="d-flex flex-column w-100 px-3">
                        <div class="d-flex justify-content-between align-items-center ">
