                            </router-link>
                        </div>
                        <input type="search" class="carrier-form__search" v-model="searchedBus" >
                        <div class="carrier-form__list">
                            <div v-for="(bus,index) in filteredBusList" :key="bus.id" class="d-flex justify-content-between px-2 mb-2">
                                <span>{{bus.name}}</span><button @click.prevent="deleteBus(index,bus.id)" class="delete-item__btn"></button>
                            </div>
                        </div>

                    </div>
                </div>
                <div class="d-flex flex-column carrier-inner__column">
                    <div class="d-flex flex-column w-100 px-3">
                        <div class="d-flex justify-content-between align-items-center ">
                            <label for="driver-list" >Водії:</label>
                            <router-link to="/manager/driver/create" class="add-button">
                                + Додати
                            </router-link>
                        </div>
                        <input type="search" class="carrier-form__search" v-model="searchedDriver">
                        <div class="carrier-form__list">
                            <div v-for="(driver,index) in filteredDriverList" :key="driver.id"  class="d-flex justify-content-between px-2 mb-2">
                                <span>{{driver.name}}</span><button @click.prevent="deleteDriver(index,driver.id)" class="delete-item__btn"></button>
