        <div class="bus-create__buttons d-flex mb-5">
            <button role="submit" class="d-flex align-items-center justify-content-center save-button" @click="addNewCarrier">
                <img src="@/assets/images/icons/save.svg" alt="save">
                <span>Зберегти і вийти</span>
            </button>
            <button role="button" class="d-flex align-items-center justify-content-center decline-button" @click="declineNewCarrier">
                <img src="@/assets/images/icons/decline.svg" alt="decline">
                <span>Відмінити</span>
            </button>
        </div>
        <ModalConfirm 
            :overlayIsVisible="modalDeclineIsVisible"
            title="Відмінити зроблені зміни?"
            textAccept="Так"
            textDecline="Ні"
            @status="declineNewCarrier"
            @success="declineTrue"
        />
    </div>
</template>

<script>
import { TheMask } from "vue-the-mask";
import ModalConfirm from "../../components/ModalConfirm";

export default {
    name: '',
    data(){
        return {
            // newCarrier: {
                id: '',
                name: '',
                address: '',
                phone: '',
                contact_person: '',
                account: '',
                trips: [],
                buses: [],
                drivers: [],
                liqpay: '',
            // },
            searchedBus: '',
            searchedTrip: '',
            searchedDriver: '',
            modalDeclineIsVisible: false,
        }
    },
    components: {
        TheMask,
        ModalConfirm
    },
    created(){
        // axios.get('admin/carrier/form')
        //     .then((res) => {
        //         console.log(res.data.result);
        //     })

        if (this.$route.params.id != 'create'){
            axios.get('admin/carrier/'+this.$route.params.id)
                .then((res) => {
                //    console.log(res.data.result);
                    this.id = res.data.result.id
                    this.name = res.data.result.name
                    this.address = res.data.result.address
                    this.phone = res.data.result.phone
                    this.contact_person = res.data.result.contact_person
                    this.liqpay = res.data.result.liqpay
                    this.buses = res.data.result.buses
                    this.trips = res.data.result.trips
                    this.drivers = res.data.result.drivers
                })
        } else if(this.$route.params.id === 'create'){
            // axios.get('admin/carrier')
            //     .then((res) => {
            //     //    console.log(res.data.result);
            //         this.newCarrier.name = res.data.result.name
            //         this.newCarrier.address = res.data.result.address
            //         this.newCarrier.phone = res.data.result.phone
            //         this.newCarrier.contact_person = res.data.result.contact_person
            //         this.newCarrier.liqpay = res.data.result.liqpay
            //         this.newCarrier.buses = res.data.result.buses
            //         this.newCarrier.trips = res.data.result.trips
            //         this.newCarrier.drivers = res.data.result.drivers
            //     })
        }
    },
    methods: {
        declineNewCarrier(status){
            this.modalDeclineIsVisible = status         
        },
        addNewCarrier(){
            const data = {
                name: this.name,
                address: this.address,
                phone: this.phone,
                contact_person: this.contact_person,
                liqpay: this.liqpay,
            }
            if(this.id != ''){
                axios.post("admin/carrier", data)
                    .then(res => {
                        location.href ='/manager/carrier/'
                    })
            } else {
                axios.post(`admin/carrier/${this.id}`, data)
                    .then(res => {
                        location.href ='/manager/carrier/'
                    })
            }
            
        },
        declineTrue(success){
            if(success){
                location.href ='/manager/carrier/'
            }            
        },
        deleteTrip(index,id){
            this.trips.splice(index,1)
            axios.delete(`admin/trip/${id}`)
                .then(() => {                    
                    console.log('trip deleted')
                })
        },
        deleteDriver(index,id){
            this.drivers.splice(index,1)
            axios.delete(`admin/driver/${id}`)
                .then(() => {                    
                    console.log('driver deleted')
                })
        },
        deleteBus(index,id){
            this.buses.splice(index,1)
            axios.delete(`admin/bus/${id}`)
                .then(() => {                    
                    console.log('bus deleted')                    
                })
        },
    },
    computed: {
        filteredBusList: function(){
            return this.buses.filter(bus => {
                return bus.name.toLowerCase().includes(this.searchedBus.toLowerCase())
            })
        },
        filteredDriverList: function(){
            return this.drivers.filter(driver => {
                return driver.name.toLowerCase().includes(this.searchedDriver.toLowerCase())
            })
        },
        filteredTripList: function(){
            return this.trips.filter(trip => {
                return trip.name.toLowerCase().includes(this.searchedTrip.toLowerCase())
            })
        },
    }
}
</script>

