<template>
    <div class="content-wrapper">
        <h1>Водії<span>5</span></h1>
        <router-link
            to="/manager/driver/create"
            tag="button"
            class="add-item"
            ><span>+</span><span>Додати водія</span></router-link
        >
        <b-table
            class="mt-3 mb-3"
            striped
            hover
            :fields="fields"
            :items="items"
            @row-clicked="show"
        >
            <template #cell(id)="data">
                <span class="id-cell">{{ data.index + 1 }}</span>
            </template>
            <template #cell(driver)="data">
                <span class='bold route-name'>{{ data.item.driver }}</span>
            </template>
            <template #cell(driverId)="data">
                <span>{{ data.item.driverId }}</span>
            </template>
            <template #cell(carrier)="data">
                <span>{{ data.item.carrier }}</span>
            </template>
            <template #cell(delete)="data">
                <span
                    @click.prevent="deleteItem(data.index, data.item.id)"
                    class="delete-table__button"
                >
                  Видалити
                </span>
            </template>
        </b-table>
    </div>
</template>
<script>
export default {
    title: "Driver",
    data() {
        return {
            // Note `isActive` is left out and will not appear in the rendered table
            fields: [
                {
                    key: "id",
                    label: "№"
                },
                {
                    key: "driver",
                    label: "Водій"
                },
                {
                    key: "driverId",
                    label: "ID"
                },
                {
                    key: "carrier",
                    label: "Перевізник"
                },
                {
                    key: "delete",
                    label: ""
                }
            ],
            items: [
            ]
        };
    },
    created() {
        axios.get("admin/driver").then(res => {
            this.items = res.data.result.data;
        });
    },
    methods: {
        show(item) {
            this.$router.push(`/manager/driver/${item.id}`)
        },
        deleteItem(index, id) {
            this.items.splice(index, 1);
            axios.delete("admin/driver/" + id).then(res => {
                //TODO: Notification delete
                console.log('deleted')
            });
        }
    }
};
</script>

