                    <div  class="position-relative w-100">
                        <!-- <date-picker
                            ref="dp2"
                            name="medDate2"
                            v-model="newDriver.date_medical"
                            :config="optionsdate"
                            :clear-button="false"
                        >                        
                        </date-picker> -->
