                    <div  class="position-relative w-100">
                        <!-- <date-picker
                            ref="dp4"
                            name="licenceDate"
                            v-model="newDriver.date_licence"
                            :config="optionsdate"
                            :clear-button="false"
                        >                        
                        </date-picker> -->
                        <datepicker placeholder="Select Date" ref="dp4" name="licenceDate" v-model="date_licence" class="date-picker-input" />


                        <img
                            src="@/assets/images/icons/datapicket-icon.svg"
