<script>
// import datePicker from "vue-bootstrap-datetimepicker";
import Datepicker from 'vuejs-datepicker';
import ModalConfirm from "../../components/ModalConfirm";
import Multiselect from 'vue-multiselect'
import { TheMask } from 'vue-the-mask'

export default {
    name: "DriverCreate",
    data(){
        return {
            // newDriver:{
                surname: '',
                name: '',
                last_name: '',
                id: '',
                licence: '',
                date_licence: new Date(),
                category: [],
                carrier: [],
                carrier_id: 0,
                date_medical: new Date(),
                phone: '',
            // },
            // optionsdate: {
            //     format: "DD.MM.YYYY",
            //     useCurrent: false,
            //     minDate: new Date(),
            // },
            modalDeclineIsVisible: false,
            categoriesList: [],
            carriersList: [],
        }
    },
    components: {
        // datePicker,
        ModalConfirm,
        Multiselect,
        TheMask,
        Datepicker
    },
    methods: {
        addNewDriver(){
            // console.log(this.newDriver.carrier)
            const data = {
                surname: this.surname,
                name: this.name,
                last_name: this.last_name,
                id: this.id,
                licence: this.licence,
                date_licence: this.date_licence,
                category: this.category,
                carrier: this.carrier,
                carrier_id: this.carrier_id,
                date_medical: this.date_medical,
                phone: this.phone,
            }
            if (this.$route.params.id == 'create'){
                axios.post("admin/driver", data)
                    .then(res => {
                        location.href ='/manager/driver/'
                    })
            } else {
                axios.put(`admin/driver/${this.id}`, data)
                    .then(res => {
                        location.href ='/manager/driver/'
                    })
            }
        },
        declineNewDriver(status){            
            this.modalDeclineIsVisible = status            
        },
        declineTrue(success){
            if(success){
                location.href ='/manager/driver/'
            }            
        },
        // test(){
        //     console.log(this.newDriver)
        //     console.log(this.categoriesList)
        //     console.log(this.carriersList)
        // },
        changeCarrier(carrier){
            this.carrier_id = carrier.id
        },
        //multiselect method
        addCategoryTag (newTag) {
            const tag = {
                name: newTag,
                code: newTag.substring(0, 2) + Math.floor((Math.random() * 10000000))
            }
            this.categoriesList.push(tag)
            this.category.push(tag)
        }
    },
    created(){
        axios.get('admin/driver/form')
            .then((res) => {
                this.carriersList = Object.keys(res.data.result.carrier).map((key) => { return { id: Number(key), title: res.data.result.carrier[key]} });
                this.categoriesList = res.data.result.categories
            })

        if (this.$route.params.id != 'create'){
            axios.get('admin/driver/'+this.$route.params.id)
                .then((res) => {
                   console.log(res.data.result);

                this.id = res.data.result.id

                this.first_name = res.data.result.first_name
                this.surname = res.data.result.surname
                this.last_name = res.data.result.last_name

                this.phone = res.data.result.phone
                this.licence = res.data.result.licence
                this.date_medical = res.data.result.date_medical
                // this.newDriver.carrier = res.data.result.carrier
                this.carrier_id = Object.keys(res.data.result.carrier)[0]
                this.carrier = Object.keys(res.data.result.carrier).map((key) => { return { id: Number(key), title: res.data.result.carrier[key]} });
                this.date_licence = res.data.result.date_licence
                this.category = res.data.result.categories;

            })
        } 

        
    },
}
</script>

