        Multiselect,
    },
    methods: {
        chooseFunctional(value){
            if(this.comfort.includes(value)){
                this.comfort.splice(this.newBus.comfort.indexOf(value), 1)
            } else {
                this.comfort.push(value)
            }            
        },
        addNewBus(){
            

            axios.post("admin/bus", {
                carrier_id : this.carrier_selected.id,
                schema_id : this.schema_selected.id,
                comfort : this.comfort,
                number : this.number,
                numbering: this.schema,
                seats: this.seats
            })
                .then(() => {
                    location.href ='/manager/bus/'
                    // console.log('ok')
                })
        },
        declineBus(status){ 
            this.modalDeclineIsVisible = status            
        },
        declineTrue(success){
            if(success){
                location.href ='/manager/bus/'
            }            
        },
        deleteBus(status){
            this.modalDeleteIsVisible = status
        },
        deleteTrue(success){
            if(success){
                axios.delete("manager/bus/" + this.id)
                    .then(() => {
                        location.href ='/manager/bus/'
                    })
            }            
        },
        test(){
            console.log(this)

            console.log(schema[1][1].num)
        },
        showSeatInfo(line,seat, value){
            this.$set(this.schema[line][seat], 'hidden', !value)
            // this.newBus.schema.template[line][seat].hidden = !this.newBus.schema.template[line][seat].hidden
        },
        changeSeatStatus(line,seat, value){
            let changedStatus = value === 'free' ? 'booked' : 'free'
            this.$set(this.schema[line][seat], 'status', changedStatus)
            // console.log(this.schema[line][seat].status)
        },
        chooseSchema(schema){
            console.log(schema)
            // console.log(this.schema_selected)

            axios.get(`admin/schema/${schema.id}`)
                .then((res) => {
                   console.log(res.data);
                })
        }
    },
    created(){
        
        const self = this
        if (this.$route.params.id != 'create'){
            axios.get('admin/bus/form')
                .then((res) => {
                //    console.log(res.data.result);

                   self.carriersList =  Object.keys(res.data.result.carrier).map((key) => { return { id: Number(key), name: res.data.result.carrier[key]} });
                   self.schemasList = Object.keys(res.data.result.schema).map((key) => { return { id: Number(key), name: res.data.result.schema[key]} });
                })
                .then()
            axios.get('admin/bus/' + this.$route.params.id)
                .then((res) => {
                    // console.log(res.data.result);

                    //temporally
                    self.brandsList = [res.data.result.brand,res.data.result.brand],
                    self.modelsList = [res.data.result.model,res.data.result.brand],
                    //end of temporally

                    // self.newBus.id = ''
                    self.brand = res.data.result.brand
                    self.model = res.data.result.model
                    self.number = res.data.result.number
                    // self.newBus.carrier_id = res.data.result.carrier.id
                    // self.carrier_selected = [...res.data.result.carrier]
                    this.$set(this, 'carrier_selected', res.data.result.carrier)

                    let lines = [...res.data.result.schema.template]

                    lines = lines.map(item => {  
                        return item.map(inner => {
                            if(inner.type === 'seat' ){ 
                                return {
                                    ...inner,
                                    hidden: false
                                }
                        } else {
                            return inner
                        }
                        })
                    })

                    this.$set(this, 'schema', lines)


                    // self.newBus.schema_id = res.data.result.schema.id
                    self.schema_id = res.data.result.schema.id
                    self.schema_name = res.data.result.schema.name
                    self.passengers = 0

                    self.comfort = res.data.result.comfort
                })

            
        } else {
            axios.get('admin/bus/form')
                .then((res) => {
                    // console.log('create bus');
                    // console.log(res.data.result);

                    // console.log(this.$route.params)


                    self.carriersList = Object.keys(res.data.result.carrier).map((key) => { return { id: Number(key), name: res.data.result.carrier[key]} });
                    self.schemasList = Object.keys(res.data.result.schema).map((key) => { return { id: Number(key), name: res.data.result.schema[key]} });
                })
        }

        
    }
}
</script>

