                        // console.log(response.routes[0].legs[i].start_location.lat())
                        // console.log(response.routes[0].legs[i].start_location.lng())


                        const latlng = {
                            lat: parseFloat(response.routes[0].legs[i].start_location.lat()),
                            lng: parseFloat(response.routes[0].legs[i].start_location.lng()),
                        };
                        geocoder.geocode({ location: latlng }, (results, status) => {
                            if (status === "OK") {
                            if (results[0]) {

                                // console.log(results[0].address_components)

                                let res = '';
                                    

                                results[0].address_components.forEach(item => {
                                    if(item.types[0] === 'locality'){
                                        res = item.long_name + ',' + res
                                    }                    
                                    
                                    if(item.types[0] === 'administrative_area_level_1'){
                                        res = res + item.long_name + ','
                                    }

                                    if(item.types[0] === 'country'){
                                        res += item.long_name
                                    }                                     
                                })
                            
                                self.geoMarkers[i].location = res
                            }}
                        });

                        if(i == response.routes[0].legs.length-1){
                            self.geoMarkers[i+1].location = response.routes[0].legs[i].end_address
                            // console.log(response.routes[0].legs[i])

                            const latlnglast = {
                                lat: parseFloat(response.routes[0].legs[i].end_location.lat()),
                                lng: parseFloat(response.routes[0].legs[i].end_location.lng()),
                            };
                            geocoder.geocode({ location: latlnglast }, (results, status) => {
                                // console.log(latlng)
                                if (status === "OK") {
                                if (results[0]) {

                                    // console.log(results[0].address_components)

                                    let res = '';
                                        

                                    results[0].address_components.forEach(item => {
                                        if(item.types[0] === 'locality'){
