                return {
                        location: item.location
                    }
            })
            return tempWaypoints
        },
        intermediateGeoMarkers: function(){
            if(this.geoMarkers.length > 2){
                let tempGeo = [...this.geoMarkers]
                tempGeo.pop()
                tempGeo.shift()

                return tempGeo
            }            
        }
