        getLocation(e){
            const self = this;
            self.$options.DirectionsRenderer.setMap(self.$refs.mapRef.$mapObject)

            const geocoder = new google.maps.Geocoder();

            const latlng = {
                lat: parseFloat(e.latLng.lat()),
                lng: parseFloat(e.latLng.lng()),
            };

            if(self.geoMarkers.length === 1){
                geocodeLatLng(geocoder, this.$refs.mapRef.$mapObject, )

                function geocodeLatLng(geocoder, map) {
                    geocoder.geocode({ location: latlng }, (results, status) => {
                        if (status === "OK") {
                            if (results[0]) {
                                self.markers.push({
