                geocodeLatLng(geocoder, this.$refs.mapRef.$mapObject, )

                function geocodeLatLng(geocoder, map) {

                    geocoder.geocode({ location: latlng }, (results, status) => {
                        if (status === "OK") {
                            if (results[0]) {
                                 self.markers.push({
                                    position: latlng,
