                                })

                                let res = '';

                                results[0].address_components.forEach(item => {
                                    if(item.types[0] === 'locality'){
                                        res = item.long_name + ',' + res
                                    }                    
                                    
                                    if(item.types[0] === 'administrative_area_level_1'){
                                        res = res + item.long_name + ','
                                    }

                                    if(item.types[0] === 'country'){
                                        res += item.long_name
                                    } 
                                    
                                })

                                self.geoMarkers.splice(-2, 1, {
                                    place_id: results[0].place_id,
                                    // location: results[0].formatted_address,
                                    location: res,
                                    distance: '',
                                    price: "",
                                    place_name: '',
                                })

                                self.creatingEndStation = false
                                self.allowAddEndStation = false
                                self.endPositionsAdded = true

                                self.drawGoogleMapDirection()
                            }
                        }
                    })
                }   
            }
            //If we are creating intemediate station
            else if(self.geoMarkers.length === 2){
                geocodeLatLng(geocoder, this.$refs.mapRef.$mapObject, )

                function geocodeLatLng(geocoder, map) {
                    geocoder.geocode({ location: latlng }, (results, status) => {
                        if (status === "OK") {
                            if (results[0]) {

                                let res = "";

                                results[0].address_components.forEach(item => {
                                    if(item.types[0] === 'locality'){
                                        res = item.long_name + ',' + res
                                    }                    
                                    
                                    if(item.types[0] === 'administrative_area_level_1'){
                                        res = res + item.long_name + ','
                                    }

                                    if(item.types[0] === 'country'){
                                        res += item.long_name
                                    } 
                                    
                                })

                                self.geoMarkers.splice(self.geoMarkers.length-1, 1,  {
                                    place_id: results[0].place_id,
                                    // location: results[0].formatted_address,
                                    location: res,
                                    distance: '',
                                    price: "",
                                    place_name: '',
                                })

                                self.creationNewStationBlocking = true;
                                self.endPositionsAdded = true

                                self.drawGoogleMapDirection()
                            } else {
                                console.log("No results found");
                            }
                        } else {
                            console.log("Geocoder failed due to: " + status);
                        }
                    });
                }
            }
        },
        acceptStation(){
            this.creatingNewStation = false;

            // this.drawGoogleMapDirection();
        },

        //google maps methods
        calculateAndDisplayRoute(service, DirectionsRenderer, start, destination) {
            const self = this;

            service.route({
                origin: start,
                destination: destination,
                waypoints:  self.waypoints,
                travelMode: 'DRIVING'
            }, function(response, status) {
                //distances between markers

                if(response.routes && response.routes[0].legs){

                        for(let i = 0; i <= response.routes[0].legs.length-1; i++){
                            self.geoMarkers[i+1].distance = response.routes[0].legs[i].distance.text
                            // self.geoMarkers[i+1].duration = response.routes[0].legs[i].duration.value
                        }

                    self.markers = []
                    self.calculateTotalDistance()                    
                }
                if (status === 'OK') {
                    //don't show renderer's markers
                    // DirectionsRenderer.setOptions({ suppressMarkers: true });
                    DirectionsRenderer.setDirections(response);
                } else {
                    window.alert('Directions request failed due to ' + status);
                }
            });
        },
        test(){
            console.log(this.geoMarkers)
            // console.log(this.totalDistance)
            // console.log(this.intermediateGeoMarkers)

            // const geocoder = new google.maps.Geocoder();

            // geocoder.geocode({ placeId: "ChIJ9_C0zZvgLUcRftM9C2PlJ38" }, (results, status) => {
            //     let res = '';

            //     results[0].address_components.forEach(item => {
            //         if(item.types[0] === 'locality'){
            //             res = item.long_name + ',' + res
            //         }                    
                    
            //         if(item.types[0] === 'administrative_area_level_1'){
            //             res = res + item.long_name + ','
            //         }

            //         if(item.types[0] === 'country'){
            //             res += item.long_name
            //         } 
            //     })
            //     console.log(res)
            // })
        },
        deleteStartStation(){
            this.geoMarkers = this.geoMarkers.splice(1, this.geoMarkers.length-1)
            this.drawGoogleMapDirection();
        },
        deleteEndStation(){
            this.geoMarkers = this.geoMarkers.splice(0, this.geoMarkers.length-1)
            this.drawGoogleMapDirection();
        },
        inputIntermediatePlace(index, place){
            this.geoMarkers[index].place_name = place;
        },
        setPrices(){
            // this.$store.commit('addRoute', { 
            //    routeData: [this.startStation, ...this.geoMarkers, this.endStation ]
            //     routeData: this.geoMarkers
            // })

            this.pricesData = this.geoMarkers.map((station, index) => {

                let tempObj = {
                    start: station.location,
                    prices: []
                }

                for( let key in this.geoMarkers){

                    if(station.location == this.geoMarkers[key].location){
                        continue 
                    } else{
                        console.log(station)
                        console.log(station.distance)
                        tempObj.prices.push({
                            from: station.location,
                            to: this.geoMarkers[key].location,
                            from_place_id: station.place_id,
                            to_place_id: this.geoMarkers[key].place_id,
                            price: this.geoMarkers[index].price[key],
                        })
                    }
                }
                return tempObj;
            })


            this.showSetPrices = true

            // this.$router.push('/manager/route/set_price')
        },
        calculateTotalDistance(){
            let sum = 0;
            this.geoMarkers.forEach( (el,index) => {
                if(index === 0){
                    return 
                }
                if(isNaN(parseFloat(el.distance.replace(/,/g, '.').slice(0, -3)))){
                    sum += 0
                } else {
                    sum += parseFloat(el.distance.replace(/,/g, '.').slice(0, -3))
                }
            })

            if(isNaN(sum)){
                return ''
            }
            this.totalDistance = sum + ' км'
        },
        returnButton(){
            this.$router.go(-1)
        },
        setPrice(index, ind){
            console.log(this.pricesData[index].prices[ind].price)
        },
        acceptPrice(){
            // axios.post('admin/route')
            // .then( (res) => {
            //     console.log()
            // })
            // .catch(error => console.error(error))
        }

    },
    components: {
        StationForm,
        ModalConfirm,
        // vSelect,
    },

}
</script>



