<script>
import datePicker from "vue-bootstrap-datetimepicker";
import { TheMask } from "vue-the-mask";
import ModalConfirm from "../../components/ModalConfirm";

export default {
    name: "SuperAdminCreate",
    data() {
        return {
            // newEmployee: {
                id: null,
                surname: "",
                name: "",
                middlename: "",
                birthDate: "",
                email: "",
                phone: "",
                password: "",
                position: "",
                possibilities: " ",
                medTo: new Date(),
                salary: "",
            // },
            optionsdate: {
                format: "DD.MM.YYYY",
                useCurrent: false,
                minDate: new Date()
            },
            passwordValid: false,
            modalDeclineIsVisible: false,

        };
    },
    components: {
        datePicker,
        TheMask,
        ModalConfirm,
    },
    methods: {
        addNewEmployee() {
            const data = {
                id : this.id,
                surname :  this.surname,
                name :  this.name,
                middlename : this.middlename,
                birthDate :  this.birthDate,
                email :  this.email,
                phone :  this.phone,
                password :  this.password,
                position :  this.position,
                possibilities :  this.possibilities,
                medTo :  this.medTo,
                salary : this.salary,
            }
            axios.post("admin/create", data)
                .then(res => {
                    location.href ='/manager/superadmin/'
                })
        },
        declineNewEmployee(status) {
            this.modalDeclineIsVisible = status
        },
        declineTrue(success){
            if(success){
                location.href ='/manager/superadmin/'
            }            
        },
        emailCheck(){            
            const reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            this.passwordValid =    reg.test(this.email);
        }
    }, 
    created(){
        if (this.$route.params.id != 'create'){
            axios.get('admin/superadmin/'+this.$route.params.id)
                .then((res) => {
                   console.log(res.data.result);
                })
        }
    }
};
</script>

